import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

class App extends Component {
  constructor(){
    super()
    this.state = {
      "breakTime": 5,
      "seshTime": 25
    }
    this.handleBreakIncrement = this.handleBreakIncrement.bind(this)
    this.handleBreakDecrement = this.handleBreakDecrement.bind(this)
    this.handleSeshIncrement = this.handleSeshIncrement.bind(this)
    this.handleSeshDecrement = this.handleSeshDecrement.bind(this)
  }
  handleBreakIncrement() {
    this.setState(prevState => {
      return {breakTime: prevState.breakTime + 1}
    })
  }
  handleBreakDecrement() {
    if (this.state.breakTime === 0) {
      this.setState({breakTime: 0})
    }
    else {
      this.setState(prevState => {
        return {breakTime: prevState.breakTime - 1}
      })
    }
  }
  handleSeshIncrement() {
    this.setState(prevState => {
      return {seshTime: prevState.seshTime + 1}
    })
  }
  handleSeshDecrement() {
    if (this.state.seshTime === 0){
      this.setState({seshTime: 0})
    }
    else {
      this.setState(prevState => {
        return {seshTime: prevState.seshTime - 1}
      })
    }
  }
  render() {
    return (
      <div>
        <header id="Header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1>Steven's Pomodoro Clock</h1>
        </header>
        <main id="Main">

          <div className="Break-Sesh">
            <div className="flex-third">
              <h3 id="break-label">Break Length</h3>
              <p class="incDec" id="break-increment" onClick={this.handleBreakIncrement}>+</p>
              <p id="break-length">{this.state.breakTime}</p>
              <p class="incDec" id="break-decrement" onClick={this.handleBreakDecrement}>-</p>
            </div>

            <div className="flex-third" id="timer">
              <h3 id="timer-label">Session</h3>
              <div id="time-left">
              <p>25:00</p>
              </div>
              <div className="timer-buttons">
                <p id="start_stop">Start/Stop</p>
                <p id="reset">Reset</p>
              </div>
            </div>

            <div className="flex-third">
              <h3 id="session-label">Session Length</h3>
              <p class="incDec" id="session-increment" onClick={this.handleSeshIncrement}>+</p>
              <p id="session-length">{this.state.seshTime}</p>
              <p class="incDec" id="session-decrement" onClick={this.handleSeshDecrement}>-</p>
            </div>
          </div>
        </main>
      </div>
    );
  }
}

export default App;
